import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import ProductItem from './ProductItem'
import axios from 'axios';
import CircularProgress from '@material-ui/core/CircularProgress';
import IconButton from "@material-ui/core/IconButton";
import Consts from "../consts";

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  scrollOnTop: {
    opacity: "0.3",
    position: 'fixed',
    bottom: theme.spacing(0),
    right: theme.spacing(0),
    "&:active": {
      opacity: "1",
    }
  },
  progress: {
    position: 'fixed',
    left: "50%",
    top: "50%",
  }
}));

export default function ProductList(props) {
  const classes = useStyles();
  const [listProps, setListProps] = useState({
    products: [],
    lastPage: 0,
    isFetching: false,
    queryName: Consts.SET_OF_MENUS[props.menuIndex ? props.menuIndex : 0].queryName,
    hasNext: true,
  });
  const pageSize = 20;
  // console.log("[ProductList] menu:" + listProps.queryName + " loading:" + listProps.isFetching + " lastPage:" + listProps.lastPage + " products:" + listProps.products.length);

  useEffect(() => {
    function handleScroll() {
      const { innerHeight } = window;
      const { scrollHeight } = document.body;
      // IE에서는 document.documentElement 를 사용.
      const scrollTop =
        (document.documentElement && document.documentElement.scrollTop) ||
        document.body.scrollTop;

      if (scrollHeight - innerHeight - scrollTop >= 100 || listProps.isFetching || !listProps.hasNext) return;
      setListProps(
        {
          ...listProps,
          isFetching:true
        });
    }
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [listProps, listProps.isFetching]);

  useEffect(() => {
    let unmounted = false;
    let source = axios.CancelToken.source();

    function fetchMoreListItems() {
      var newPage = listProps.lastPage + 1;

      //console.log("[fetchItems] start "  + newPage + " " + productType);
      axios.get('https://api.smartconsumer.kr/api/getProducts?page=' + newPage + '&size=' + pageSize  + '&name=' + listProps.queryName,
        {
          cancelToken: source.token,
          timeout: 60000
        })
        .then(res => {
          if (!unmounted) {
            if (res.data.products.length > 0) {
              setListProps(prevState => (
                {
                  queryName: listProps.queryName,
                  lastPage: newPage,
                  products: [...prevState.products, ...res.data.products],
                  isFetching: false,
                  hasNext: true,
                }));
            } else {
              setListProps(prevState => (
                {
                  ...listProps,
                  hasNext: false,
                  isFetching: false,
                }));
            }
          }
        }).catch(e => {
          if (!unmounted) {
            if (axios.isCancel(e)) {
              console.log(`request cancelled:${e.message}`);
            } else {
              console.log("another error happened:" + e.message);
            }
          }
        });
    }
    // console.log("[useEffect] fetchMoreListItems");
    if (!listProps.isFetching || !listProps.hasNext)
      return;
    fetchMoreListItems();

    return function () {
      unmounted = true;
      source.cancel("Cancelling in cleanup");
    };
  }, [listProps, listProps.queryName, listProps.isFetching, listProps.lastPage]);

  useEffect(() => {
    // console.log("[useEffect] changed menu Index:", props.menuIndex);
    setListProps(
      {
        queryName: Consts.SET_OF_MENUS[props.menuIndex ? props.menuIndex : 0].queryName,
        isFetching: true,
        products:[],
        lastPage:0,
        hasNext: true,
      });
  },[props.menuIndex]);

  function handleChange(event, menuIndex) {
    if (props.menuIndex !== menuIndex) {
      props.onChangeMenu(menuIndex);
    }
  }

  function scrollToTop() {
    window.scroll(0, 0);
  }

  return (
    <div>
      <Tabs value={props.menuIndex ? props.menuIndex : 0} onChange={handleChange}
            variant="scrollable"
            scrollButtons="on">
        {
          Consts.SET_OF_MENUS.map((item, index) => (
            <Tab key={index} label={item.displayName} />
          ))
        }
      </Tabs>
      <Grid container justify="center" className={classes.root}>
        <Grid container justify="center" spacing={2}>
          {
            listProps.products.map(
              (item) => (
                <Grid key={item.cid} item>
                  <ProductItem item={item}/>
                </Grid>
              )
            )
          }
        </Grid>
        <Grid container justify="center" spacing={2}>
          { listProps.isFetching && <CircularProgress className={classes.progress} disableShrink={true}/>}
        </Grid>
        <IconButton classes={{root: classes.scrollOnTop}} onClick={scrollToTop}>
          <img src={process.env.PUBLIC_URL + "/assets/icons/arrow52.png"} alt="Scroll To Top"/>
        </IconButton>
      </Grid>
    </div>
  )
}
