function initilaizeMenus() {
  var menus = [];
  var menuSplit = String(process.env.REACT_APP_MENUS).split(';');

  menuSplit.forEach(function (item, index) {
    var menuItem = String(item).split(':');
    menus.push({displayName:menuItem[0], queryName:process.env.REACT_APP_PRODUCT_PREFIX + menuItem[1]});
  });

  return menus
}

